.out {
  background-image: linear-gradient(135deg, #362b5d 0%, #3f679e 75%, #3f679e 100%);
  line-height: 1.2;
  color: #ffffff;
}

.out a {
  text-decoration: none;
}

.out img {
  max-width: 100%;
}

.out .closed {
  display: none;
}

.out .chat {
  position: fixed;
  right: 40px;
  bottom: 25px;
  z-index: 2;
}

.out .chat a {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  background-color: #9d5e9b;
  box-shadow: 0 0 10px 3px #9b6c9b;
  border-radius: 50%;
}

.out .chat a:before {
  content: '';
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  width: 50px;
  height: 37px;
  border-radius: 5px;
  background-color: #fff;
}

.out .chat a:after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: 21px;
  bottom: 10px;
  border: 7px solid;
  border-color: #fff transparent transparent #fff;
}

.header {
  position: relative;
  top: 30px;
  left: 40px;
  right: 40px;
  width: calc(100% - 80px);
  overflow: hidden;
  border-radius: 10px;
  z-index: 2;
}

.header__wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.header__logo {
  position: relative;
  padding: 5px;
  height: 60px;
  width: 60px;
}

.header__logo span {
  display: inline-block;
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 48px;
  height: 48px;
}

.header__logo span .icon-heart {
  width: 100%;
  height: 100%;
  overflow: visible;
  animation: pulse 1s ease infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.header__lang {
  padding-left: 120px;
}

.header__lang li {
  display: inline-block;
  padding: 0 10px;
}

.header__lang li a {
