}

.header__search {
  padding-right: 120px;
}

.header__search__icon {
  display: inline-block;
  width: 27px;
  height: 27px;
}

.header__search__icon .icon-search {
  fill: #C5D5E7;
  width: 100%;
  height: 100%;
