/* Modal animation */
@-webkit-keyframes slideIn {
  from {
    top: 1000px;
    opacity: 0;
  }
  to {
    top: 130px;
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    top: 1000px;
    opacity: 0;
  }
  to {
    top: 130px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

